.post {
    background-color: white;
    margin: 30px;
    border: 1px solid lightgrey;
    max-width: 700px;
}

.post_username {
    background-color: white;
}

.post_image {
    background-color: white;
    max-height: 500px;
    width: 100%;
    object-fit: contain;
}

.post_header {
    background-color: white;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 7px;
}

.post_avatar {
    position: inherit !important;
    margin-right: 10px;
}

.post_text {
    text-align: left;
    padding-left: 10px;
    font-weight: normal;
}

.vert_icon {
    position: inherit !important;
    margin-left: auto !important;
}