body {
    margin: 0;
    background-color: #FAFAFA;
}

.app_header {
    position: sticky;
    top: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 30px;
    padding-right: 30px;
    background-color: white;
    border-bottom: 1px solid lightgrey;
}

.app_headerImage {
    margin-top: 5px !important;
    object-fit: contain;
    background-color: white;
}

.btn {
    float: right !important;
    margin-left: 10px !important;
}

.upload {
    position: sticky;
    width: 100%;
    bottom: 0;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.set_post {
    display: flex;
    flex-direction: column;
}